import React, { useEffect } from 'react';
import Form from 'components/Common/Form';
import { StyledButton, StyledTitle } from './RemittanceSetCard.style';
import useRemittance from 'hooks/Remittance/useRemittance';
import AccountForm from 'components/Common/AccountForm';
import ModalPortal from 'components/Common/Modal/Portal';
import Modal from 'components/Common/Modal/Modal';
import getCompany from 'util/getCompany';
import makeAccountNumber from 'util/makeAccountNumber';

const RemittanceSetCard = (): JSX.Element => {
  const {
    onChangeMoney,
    moneyError,
    account,
    customTitleInputStyle,
    checkStorage,
    money,
    onChangeAccountNumber,
    customInputStyle,
    accountError,
    onClickNext,
    onClickModalYes,
    getAccountInfo,
    openModal,
    setOpenModal,
    balance,
    modalInfo,
  } = useRemittance();

  useEffect(() => {
    checkStorage();
    getAccountInfo();
  }, [checkStorage]);

  return (
    <>
      <StyledTitle></StyledTitle>
      <Form
        title=" "
        placeholder=" "
        name="account"
        maxLength={14}
        error={accountError}
        onChange={onChangeAccountNumber}
        value={account}
        customInputStyle={{ marginBottom: '8px' }}
        customTitleInputStyle={customTitleInputStyle}
      />
      <AccountForm
        title=" "
        value={money}
        name="account"
        error={moneyError}
        onChange={onChangeMoney}
        placeholder=" "
        maxLength={10}
        balance={balance}
        customInputStyle={customInputStyle}
      />
      <StyledButton onClick={onClickNext}></StyledButton>

      {openModal && (
        <ModalPortal>
          <Modal
            title={getCompany(modalInfo as string)}
            content={`${makeAccountNumber(
              modalInfo as string,
            )} ${money} ?`}
            handleNo={() => {
              setOpenModal(false);
            }}
            handleYes={onClickModalYes}
          />
        </ModalPortal>
      )}
    </>
  );
};

export default RemittanceSetCard;
