import { StyledTitle } from './Register.style';
import Form from 'components/Common/Form';
import useRegister from 'hooks/Register/useRegister';
import Term from './Term';
import Button from 'components/Common/Button';
import { useEffect } from 'react';

const Register = () => {
  const {
    checkTerm,
    setCheckTerm,
    registerState,
    errorState,
    onChangeRegisterState,
    onClickRegister,
    resetAllState,
  } = useRegister();

  useEffect(() => {
    resetAllState();
  }, []);

  return (
    <div>
      <StyledTitle></StyledTitle>
      <Form
        onChange={onChangeRegisterState}
        title=""
        placeholder=" "
        name="name"
        value={registerState.name}
        error={errorState.name}
        customInputStyle={{ marginBottom: '4px' }}
      />
      <Form
        onChange={onChangeRegisterState}
        title=""
        placeholder=" "
        name="id"
        value={registerState.id}
        error={errorState.id}
        customInputStyle={{ marginBottom: '4px' }}
      />
      <Form
        onChange={onChangeRegisterState}
        title=""
        placeholder=" "
        type="password"
        name="pw"
        value={registerState.pw}
        error={errorState.pw}
        customInputStyle={{ marginBottom: '4px' }}
      />
      <Form
        onChange={onChangeRegisterState}
        title=" "
        placeholder="  "
        type="password"
        name="rePw"
        value={registerState.rePw}
        error={errorState.rePw}
        customInputStyle={{ marginBottom: '4px' }}
      />
      <Form
        onChange={onChangeRegisterState}
        title=""
        placeholder=" "
        name="phone"
        value={registerState.phone}
        error={errorState.phone}
        maxLength={13}
        customInputStyle={{ marginBottom: '4px' }}
      />
      <Form
        onChange={onChangeRegisterState}
        title=""
        placeholder="(7) "
        name="birth"
        value={registerState.birth}
        error={errorState.birth}
        maxLength={8}
        customInputStyle={{ marginBottom: '4px' }}
      />
      <Term onClick={() => setCheckTerm((prev) => !prev)} check={checkTerm} />
      <Button children="" handleClick={onClickRegister} />
    </div>
  );
};

export default Register;
