const getCompany = (number: string) => {
  const head: string = number.substring(0, 3);
  let company: string = '';
  switch (head) {
    case '001':
      company = '';
      break;
    case '002':
      company = '';
      break;
    case '003':
      company = '';
      break;
    default:
      company = '2';
      break;
  }
  return company;
};

export default getCompany;
