import AuthForm from 'components/Common/AuthForm';
import useGetPassword from 'hooks/GetPassword/useGetPassword';
import React, { useEffect } from 'react';
import { UGetPassword } from 'types/common/common.type';
import { StyledButton, StyledTitle } from './GetPassword.style';

interface IGetPasswordProps {
  title: string;
  onClick: () => void;
  type: UGetPassword;
  error: string;
  onChange: (res: string) => void;
}

const GetPassword = ({
  title,
  onClick,
  type,
  error,
  onChange,
}: IGetPasswordProps): JSX.Element => {
  const { checkStorage } = useGetPassword();

  useEffect(() => {
    checkStorage(type);
  }, [checkStorage, type]);

  return (
    <>
      <StyledTitle>{title}</StyledTitle>
      <AuthForm
        title=" 4 "
        error={error}
        onChange={onChange}
        length={4}
        customErrorStyle={{ textAlign: 'center' }}
        customTitleStyle={{ textAlign: 'center' }}
      />
      <StyledButton onClick={onClick}></StyledButton>
    </>
  );
};

export default GetPassword;
