import React, { useEffect } from 'react';
import { StyledButton, StyledTitle } from './BringMoney.style';
import AccountForm from 'components/Common/AccountForm';
import ModalPortal from 'components/Common/Modal/Portal';
import Modal from 'components/Common/Modal';
import useBring from 'hooks/Bring/useBring';

const BringMoney = (): JSX.Element => {
  const {
    onChangeMoney,
    customInputStyle,
    money,
    onClickNext,
    moneyError,
    checkStorage,
    openModal,
    setOpenModal,
    onClickModalYes,
  } = useBring();

  useEffect(() => {
    checkStorage();
  }, [checkStorage]);

  return (
    <>
      <StyledTitle></StyledTitle>
      <AccountForm
        title=" "
        value={money}
        name="account"
        error={moneyError}
        onChange={onChangeMoney}
        placeholder=" "
        maxLength={10}
        balance="450,000"
        customInputStyle={customInputStyle}
      />
      <StyledButton onClick={onClickNext}></StyledButton>
      {openModal && (
        <ModalPortal>
          <Modal
            title=""
            content="?"
            handleNo={() => {
              setOpenModal(false);
            }}
            handleYes={onClickModalYes}
          />
        </ModalPortal>
      )}
    </>
  );
};

export default BringMoney;
