import React, { useEffect } from 'react';
import Form from 'components/Common/Form';
import { StyledTitle } from './AddAccountGetInfo.style';
import useAddAccount from 'hooks/AddAccount/useAddAccount';
import Button from 'components/Common/Button';

const AddAccountGetInfo = (): JSX.Element => {
  const {
    phoneError,
    customButtonStyle,
    onChangePhone,
    onClickFind,
    phone,
    onResetPhone,
    checkGetInfo,
  } = useAddAccount();

  useEffect(() => {
    checkGetInfo();
  }, [checkGetInfo]);

  useEffect(() => {
    onResetPhone();
  }, []);

  return (
    <>
      <StyledTitle>
        <div>    </div>
        <div> </div>
      </StyledTitle>
      <Form
        title=""
        error={phoneError}
        value={phone}
        onChange={onChangePhone}
        name="phone"
        placeholder="  ex) 010-9088-2512"
        maxLength={13}
        customInputStyle={{marginBottom: "8px"}}
      />
      <Button
        children=""
        handleClick={onClickFind}
        customStyle={customButtonStyle}
      />
    </>
  );
};

export default AddAccountGetInfo;
