import Button from 'components/Common/Button';
import Form from 'components/Common/Form';
import useLogin from 'hooks/Login/useLogin';
import AuthForm from 'components/Common/AuthForm';
import { StyledLoginForm, StyledTitle } from './Login.style';

const Login = () => {
  const {
    id,
    pw,
    idError,
    pwError,
    easyPwError,
    isEasyLogin,
    onChangeIdState,
    onChangePwState,
    onChnageEasyPwState,
    onChangeEasyLogin,
    onClickLoginBtn,
  } = useLogin();

  return (
    <div>
      <StyledTitle>
        <div></div>
        <button onClick={onChangeEasyLogin}>
          {isEasyLogin ? '' : ''}
        </button>
      </StyledTitle>

      {isEasyLogin ? (
        <StyledLoginForm>
          <Form
            onChange={onChangeIdState}
            title=""
            placeholder=" "
            name="id"
            value={id}
            error={idError}
            customInputStyle={{ marginBottom: '4px' }}
          />
          <Form
            onChange={onChangePwState}
            title=""
            type="password"
            placeholder=" "
            name="pw"
            value={pw}
            error={pwError}
            customInputStyle={{ marginBottom: '4px' }}
          />
        </StyledLoginForm>
      ) : (
        <AuthForm
          title=" 6 "
          error={easyPwError}
          onChange={onChnageEasyPwState}
        />
      )}
      <Button children="" handleClick={onClickLoginBtn} />
    </div>
  );
};

export default Login;
