import React, { useEffect } from 'react';
import { UGetPassword } from 'types/common/common.type';
import { StyledButton, StyledTitle, StyledWrapper } from './Complete.style';
import { ReactComponent as Check } from 'assets/icon/check.svg';
import useComplete from 'hooks/Complete/useComplete';

interface ICompleteProps {
  title: string;
  type: UGetPassword;
  bringAccount?: string;
  bringMoney?: string;
  remittanceGiveAccount?: string;
  remittanceReceiveAccount?: string;
  remittanceMoney?: string;
  remittanceCommission?: string;
  handleClick: () => void;
}

const Complete = (Props: ICompleteProps): JSX.Element => {
  const { checkStorage } = useComplete();
  const {
    title,
    bringMoney,
    bringAccount,
    type,
    remittanceCommission,
    remittanceGiveAccount,
    remittanceMoney,
    remittanceReceiveAccount,
    handleClick,
  } = Props;

  useEffect(() => {
    checkStorage(type);
  }, [checkStorage, type]);

  return (
    <>
      <StyledTitle>
        <Check width={100} height={100} />
        <div className="title">{title}</div>
      </StyledTitle>
      <StyledWrapper>
        {remittanceGiveAccount && (
          <div className="line">
            <span className="property"> : </span>
            <span className="content">{remittanceGiveAccount}</span>
          </div>
        )}

        {remittanceReceiveAccount && (
          <div className="line">
            <span className="property"> : </span>
            <span className="content">{remittanceReceiveAccount}</span>
          </div>
        )}

        {remittanceMoney && (
          <div className="line">
            <span className="property"> : </span>
            <span className="content">{remittanceMoney} </span>
          </div>
        )}

        {remittanceCommission && (
          <div className="line">
            <span className="property">: </span>
            <span className="content">{remittanceCommission} </span>
          </div>
        )}

        {bringAccount && (
          <div className="line">
            <span className="property"> : </span>
            <span className="content">{bringAccount}</span>
          </div>
        )}

        {bringMoney && (
          <div className="line">
            <span className="property"> : </span>
            <span className="content">{bringMoney} </span>
          </div>
        )}
      </StyledWrapper>
      <StyledButton onClick={handleClick}></StyledButton>
    </>
  );
};

export default Complete;
