import {
  StyledStartNow,
  StyledTitle,
  StyledTopBackground,
  StyledTopHeader,
} from './Welcome.style';
import RyanGIF from 'assets/Image/RyanGIF.gif';
import Button from 'components/Common/Button';
import { StyledButton } from 'components/Welcome/Welcome.style';
import useWelcome from 'hooks/Welcome/useWelcome';

const Welcome = () => {
  const { pushRegister, pushLogin, customButtonStyle } = useWelcome();
  return (
    <div>
      <StyledTopBackground>
        <StyledTopHeader>
          <div className="title">
            <div className="mainTitle"></div>
            <div className="subTitle">in DGSW</div>
          </div>
        </StyledTopHeader>
        <StyledTitle>
          <span className="subTitle"> </span>
          <span className="mainTitle"></span>
        </StyledTitle>
        <img src={RyanGIF} alt="Welcome GIF" />
      </StyledTopBackground>
      <StyledStartNow>
        <span className="highlighting"></span>
         
      </StyledStartNow>
      <StyledButton>
        <Button
          children=""
          handleClick={pushLogin}
          customStyle={customButtonStyle}
        />
        <Button children="" handleClick={pushRegister} />
      </StyledButton>
    </div>
  );
};

export default Welcome;
